        </datalist> 
    </span>
    <span class="form-wrapper">
        <textarea v-model="message" placeholder="Message" class="form message"></textarea>
    </span>
    <button class="send btn" @click="send">Send Message</button>
</div>
</template>

<script>
export default {
    name: "ContactForm",
    data(){
        return {
            name: "",
            email: "",
            subject: "",
            message: ""
        }
    },
    methods: {
        send(){
            // Email.send({
            //     Host: "smtp.gmail.com",
            //     Username
            // })
        }
    }
}
</script>

<style lang="scss">
.contact-form{
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    gap: 0.5em;

    font-size: 24px;
    height: 100%;

    color: var(--dark);

    .btn{
        background-color: var(--regular);
        border: none;
        padding: 0.5em 2em;
        border-radius: 10em;
        color: var(--white);
        font-weight: bold;
        font-size: 18px;

        &:hover{
            cursor: pointer;
            filter: brightness(0.9);
        }
    }

    .form-wrapper{
    position: relative;
    isolation: isolate;
    border-radius: 5px;
    overflow: hidden;
    // width: fit-content;
    // height: min-content;

    &::after{
        content: "";
        
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
        
        display: block;
        width: 100%;
        height: 100%;
        
        background-color: var(--regular);
        opacity: 0.5;
    }
    .form{
        position: relative;
        border: none;
        color: var(--dark);
        background-color: transparent;
        padding: 1em 1em;
        outline: transparent;
        min-width: 20em;
        font-size: 18px;

