<template>
  <section id="teams">
    <h2>
      Our Team
    </h2>
    <div class="leadership-wrapper">
      <h3>
        Leadership
      </h3>
      <div class="leader-wrapper">
        <MemberTile v-for="(member, index) in leadership" :key="index" :member="member"/>
      </div>
    </div>

    <div class="subteams-wrapper">
      <div class="header-wrapper">
        <h3>
          Subteams
        </h3>
        <span class="learn-more">
          Meet Our Members!
        </span>
      </div>
      <div class="team-wrapper">
        <TeamTile v-for="(team, index) in teams" :key="index" :team="team"/>
      </div>
    </div>
  </section>
</template>

<script>
import MemberTile from '@/components/MemberTile.vue';
import TeamTile from '@/components/TeamTile.vue';

export default {
    name: "TeamsPreview",
    components: {
      MemberTile,
      TeamTile
    },
    computed: {
      leadership(){
        let leaders = []
        const leadership = this.$store.state.info["leadership"].members;
        const members = this.$store.state.info["members"];
        for(let i = 0; i < leadership.length; i++){
          let leader = leadership[i];
          if (leader in members){
            leaders.push({name: leader, ...members[leader]})
          }
        }
        
        return leaders
      },
      teams(){
        let teams = []
        const subteams = this.$store.state.info["teams"];
        for(const team in subteams){
          let subteam = subteams[team]
          teams.push({title: subteam.title, logo: subteam.logo})
        }

        return teams
      }
    },
}
</script>

<style lang="scss">
#teams{
  position: relative;

  background-color: var(--white);

  padding-top: 12em;

  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 3em;
  // height: 100vh;

  h2{
    margin-bottom: 0;
  }

  .leader-wrapper{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    gap: 5em;
  }

  .subteams-wrapper{
    h3{
      margin-bottom: 0.5em;
    }
  }

  .header-wrapper{
    margin-bottom: 2em;
  }

  .learn-more{
    line-height: 1em;
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    align-items: center;
    width: fit-content;
    margin: auto;

    &:hover{
      cursor: pointer;
      filter: brightness(0.9);
      text-decoration: underline;
    }

    .side-arrow{
      // width: 1.25em;
      height: 1.25em;
      box-sizing:initial;
      width: fit-content;
      fill: var(--dark);
      margin: auto;
    }
  }

  .team-wrapper{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    gap: 5em;
  }
}
</style>