import { createStore } from 'vuex'
import info from "@/data/info.json"

export default createStore({
  state: {
    info: {}
  },
  getters: {
    info: state => state.info,
  },
  mutations: {
    setInfo(state, info){
      state.info = info
    },
  },
  actions: {
    async loadInfo({commit}){
      commit("setInfo", info);

      await fetch(`${ process.env.BASE_URL }info.json`).then((res) => res.json()).then((info) => {
        commit("setInfo", info);
      }).catch((e) => {console.log(e)})
    }
  },
  modules: {
  }
})
