<template>
  <span class="status-bar"></span>
</template>

<script>
export default {
    name: "StatusBar"
}
</script>

<style lang="scss">
.status-bar{
    content: "";
    display: block;
    margin: auto;
    width: 50%;
    min-width: 10em;
    height: 2em;
    background-color: var(--light);
    overflow: hidden;
    border-radius: 5px;
    isolation: isolate;

    &::before{
        content: "";
        display: block;
        width: 25%;
        height: 100%;
        background-color: var(--dark);
        border-radius: 0 10em 10em 0;
    }
}
</style>