<template>
<div class="nav-wrapper"> 
    <div class="hamburger-menu" @click="open" :class="{open: isOpen}">
        <span class="line top"></span>
        <span class="line middle"></span>
        <span class="line bottom"></span>
    </div>
  <nav id="nav" :class="{open: isOpen}">
    <a href="#" id="logo" class="link" @click="isOpen = false">Cyclone</a>
    <a href="#" id="home-link" class="link" @click="isOpen = false">Home</a>
    <a @click="scrollTo" data-target="about" id="about-link" class="link">About</a>
    <a @click="scrollTo" data-target="robot" id="robots-link" class="link">Our Robot</a>
    <a @click="scrollTo" data-target="sponsors" id="sponsors-link" class="link">Sponsors</a>
    <a @click="scrollTo" data-target="teams" id="teams-link" class="link">Teams</a>
    <a @click="scrollTo" data-target="contact" id="contact-link" class="link">Contact Us</a>
    <span id="donate-link" class="link">Donate</span>
  </nav>
</div>
</template>

<script>
export default {
    name: "NavBar",
    data(){
        return{
            isOpen: false
        }
    },
    methods: {
        scrollTo(e){
            console.log(e.currentTarget.dataset)
           document.getElementById(e.currentTarget.dataset.target).scrollIntoView({ behavior: "smooth", block: "start", inline: "nearest"}) 
            this.isOpen = false;
        },
        open(){
            console.log("Clicked")
            this.isOpen = !this.isOpen;
        }
    }
}
</script>

<style lang="scss">
@keyframes dropdown {
    to{transform: translateY(0%);}
}

.hamburger-menu{
    display: none;
}

#nav{
    transform: translateY(-100%);

    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;

    display: flex;
    flex-flow: row;
    justify-content: flex-end;
    align-items: center;
    gap: 1.5em;

    width: 100%;
    font-size: 16px;
    padding: 1em;

    background-color: var(--white);

    animation: dropdown 2s 1s ease-in-out;
    animation-fill-mode: forwards;

    .link{
        text-decoration: none;
        color: var(--regular);
        font-weight: bold;

        &#logo{
            color: var(--medium);
            margin-right: auto;
            font-size: 18pt;
            font-family: "Righteous", cursive;
            letter-spacing: 0.05em;
            font-weight: 900;
        }

        &#donate-link{
            background-color: var(--regular);
            color: var(--white);
            padding: 0.25em 0.75em;
            border-radius: 10em;
        }

        &:hover{
            cursor: pointer;
            filter: brightness(0.9);
        }
    }
}

@media screen and (max-width: 768px) {
// .nav-wrapper{
//     max-width: 100%;
// }

    .hamburger-menu{
        position: fixed;

        display: block;
        background-color: var(--dark);
        color: var(--white);

        right: 1em;
        top: 1em;
        padding: 0.5em;
        border-radius: 10em;
        z-index: 11;
        .line{
            position: relative;
            content: "";
            display: block;
            width: 2em;
            height: 0.25em;
            background-color: var(--white);
            margin: 0.5em;
            transition: all 1s ease-in-out;
        }

        &.open{
            .top{
                transform: translateY(0.75em) rotate(-45deg);
            }
            .middle{
                opacity: 0;
            }
            .bottom{
                transform: translateY(-0.75em) rotate(45deg);
            }
        }
    }

    #nav{
        transform: translateY(-100%);
        // max-height: 0;
        animation: none;
        flex-flow: column;
        transition: all 1s ease-in-out;
        overflow: hidden;
        font-size: 24px;
        box-shadow: 0 0 5px -3px var(--dark);

        &.open{
            // max-height: 100%;
            transform: translateY(0);
        }
    }
}
</style>